import { Component, OnInit } from '@angular/core';
import { QuickSidebarComponent } from '../../../layouts/quick-sidebar/quick-sidebar.component';
import { SideBarService } from '../../../layouts/side-bar/side-bar.service';
import { SideBarToggleComponent } from '../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../_services/data.service';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { Helpers } from '../../../../helpers';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { ProductService } from '../../../../auth/_services/product.service';
import { MenuService } from '../../../../auth/_services/menu.service';
import { CategoryService } from '../../../../auth/_services/category.service';
import { SubCategoryService } from '../../../../auth/_services/subCategory.service';
import { Attributes } from '../../../../auth/_models/attributes';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';


@Component({
    selector: 'app-add-discount',
    templateUrl: "add-discount.component.html",
    styles: []
})
export class AddDiscountComponent implements OnInit {

    dropdownList = [];
    selectedItems = [];
    dropdownSettings = {};
    categories = [];
    selectedCategories = [];
    selectedProduct = [];
    product = [];
    productDrpdown = [];


    // cities =[];
    hide = '1';
    currentUser: any;
    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private _dataService: DataService, private _productService: ProductService, private _menuService: MenuService, private _categoryService: CategoryService, private _subCategory: SubCategoryService, private _script: ScriptLoaderService, private formBuilder: FormBuilder, private toastr: ToastrService, private router: Router) {

        this.form = formBuilder.group({
            category: [null],
            product: [null],
            discount_type: [null],
            discount_offer: [null],
        });
    }


    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_color != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this._dataService.getProductState().subscribe(
            data => {
                this.hide = data;
            }
        );
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
        this.categories = JSON.parse(localStorage.getItem('category'));

        this.product = JSON.parse(localStorage.getItem('product'));
        //console.log('hii',this.categories);
        /* this.cities = [
               { item_id: 1, item_text: 'Mumbai' },
               { item_id: 2, item_text: 'Bangaluru' },
               { item_id: 3, item_text: 'Pune' },
               { item_id: 4, item_text: 'Navsari' },
               { item_id: 5, item_text: 'New Delhi' }
           ];*/

        this.dropdownSettings = {
            singleSelection: false,
            idField: 'item_id',
            textField: 'item_text',
            selectAllText: 'Select All',
            unSelectAllText: 'UnSelect All',
            itemsShowLimit: 3,
            allowSearchFilter: true
        };
    }

    get product_attributes(): FormArray {
        return this.form.get('product_attributes') as FormArray;
    };

    addLair() {
        this.product_attributes.push(this.formBuilder.group(new Attributes()));
        console.log(this.form.value);
    }


    changeProductState(event) {
        // console.log(event.target.value);
        this._dataService.setProductState(event.target.value);
        this._dataService.getProductState().subscribe(
            data => {
                this.hide = data;
            }
        );
    }





    onSubmit() {

        let input: any;
        this.formSumitAttempt = true;
        if (this.form.valid) {
            input = { form_data: this.form.value };
            if (this.currentUser.success.role == 1) {


                input.form_data.admin_id = this.currentUser.success.id;
                this._productService.createDiscount(input).subscribe(
                    data => {
                        this.reset();
                    }
                );
            } else {
                input.form_data.vendor_id = this.currentUser.success.id;
                this._productService.createDiscount(input).subscribe(
                    data => {
                        this.reset();
                    }
                );
            }
        }
    }

    reset() {
        // if (this.selectedStatus != '') {
        this.form.reset();
        //this.selectedStatus = '';
        this.formSumitAttempt = false;
        // }
    }

    onItemSelect(item: any) {
        console.log(item);
        this._productService.getProduct(item).subscribe(
            data => {
                this.productDrpdown = data.success;
                // / console.log(this.productDrpdown);
            }

        )
    }

    onSelectAll(items: any) {
        console.log(items);
    }

    onDeSelectItem(item: any) {
        console.log(this.selectedItems);
    }


}
